.custom-container{
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.title{
    display: flex;
    justify-content: center;
    align-items: center;
}

.title h2{
    margin-left: 5rem;
}

.chat-container{
    border-radius: 2rem;
    border: white 1px solid;
    text-align: right;
    height: 50rem;
    width: 50%;
    margin: auto;
    margin-top: 1rem;
}

.chat{
    height: 90%;
    background-color: rgb(161, 161, 161);
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
    margin-bottom: 0.7rem;
}

.chat-container hr{
    margin-top: -0px;
}

.input-field{
    height: 4rem;
    margin: auto;
    padding-top: 0.7rem;
}

.input-field form{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.input-text{
    width: 75%;
    height: 2rem;
    padding-left: 1rem;
    border-radius: 1rem;
    border: 1px white solid;
    background-color: transparent;
    color: white;
    margin-left: 1rem;
}

.custom-button{
    border-radius: 1rem;
    background-color: rgba(0, 2, 136, 0.411);
    color: white;
    height: 2rem;
    width: 5rem;
    border: rgba(0, 4, 255, 0.651) 1px solid;
    margin-left: 2rem;
    margin-right: 1rem;
}

.custom-button:hover{
    background-color: transparent;
}

.custom-button:active{
    background-color: rgba(0, 4, 255, 0.651);
}

.message{
    display: flex;
    flex-direction: column;
}